"use client";

import { useState, useMemo } from "react";
import Link from "next/link";
import { useRouter } from "next/navigation";
import ProgressTabs from "@/components/register/ProgressTabs";
import QuestionCard from "@/components/register/QuestionCard";
import PhotoUploader from "@/components/register/PhotoUploader";

export default function RegisterFlow() {
  const router = useRouter();
  const [step, setStep] = useState(0);

  const [errors, setErrors] = useState({});
  const [form, setForm] = useState({
    name: "",
    phone: "",
    age: "",
    gender: "",
    stage: null,
    hair: {},
    internal: {},
    scalpPhoto: null
  });

  function validateStep() {
    let err = {};

    if (step === 0) {
      if (!form.name.trim()) err.name = "Name is required";
      if (!form.phone.trim()) err.phone = "Phone is required";
      if (!form.age) err.age = "Age is required";
      if (!form.gender) err.gender = "Gender is required";
    }

    if (step === 1) {
      if (form.stage === null) err.stage = "Please choose your stage";
    }

    if (step === 3) {
      if (!form.scalpPhoto) err.scalpPhoto = "Scalp photo is required";
    }

    setErrors(err);
    return Object.keys(err).length === 0;
  }

  function next() {
    if (!validateStep()) return;
    setStep((p) => p + 1);
  }

  function prev() {
    setStep((p) => Math.max(0, p - 1));
  }

  function updateField(field, value) {
    setForm((prev) => ({ ...prev, [field]: value }));
  }

  function updateNested(section, id, value) {
    setForm((prev) => ({
      ...prev,
      [section]: { ...prev[section], [id]: value }
    }));
  }

  const hairQuestions = [
    {
      id: "family",
      q: "Do you have a family history of hair loss?",
      options: ["Mother", "Father", "Both", "None"]
    },
    {
      id: "dandruff",
      q: "Do you have dandruff?",
      options: [
        "No dandruff",
        "Mild dandruff",
        "Heavy dandruff",
        "Psoriasis / Seborrheic Dermatitis"
      ]
    }
  ];

  const internalQuestions = [
    {
      id: "sleep",
      q: "How well do you sleep?",
      options: [
        "Very peaceful sleep",
        "Disturbed sleep",
        "Difficulty falling asleep"
      ]
    },
    {
      id: "stress",
      q: "How stressed are you?",
      options: ["None", "Low", "Moderate", "High"]
    },
    {
      id: "constipation",
      q: "Do you feel constipated?",
      options: [
        "No",
        "Yes (fewer than 3 stools/week)",
        "Difficulty passing stool",
        "IBS"
      ]
    },
    {
      id: "gas",
      q: "Do you have Gas or Acidity?",
      options: ["No", "Sometimes", "Often"]
    },
    {
      id: "energy",
      q: "How are your energy levels?",
      options: [
        "Always high",
        "Low in morning",
        "Low in afternoon",
        "Low by night",
        "Always low"
      ]
    },
    {
      id: "supplements",
      q: "Are you taking hair supplements?",
      options: ["Yes", "No"]
    }
  ];

  const stageImages = [
    { id: 0, label: "Stage 1 (Mild)", img: "/images/stages/male-1.svg" },
    { id: 1, label: "Stage 2 (Moderate)", img: "/images/stages/male-2.svg" },
    { id: 2, label: "Stage 3 (Severe)", img: "/images/stages/male-3.svg" }
  ];

  function submit() {
    if (!validateStep()) return;

    const qs = new URLSearchParams({
      ...form,
      scalpPhoto: form.scalpPhoto?.name || ""
    }).toString();

    router.push("/coach?" + qs);
  }

  return (
    <div className="min-h-screen py-8 bg-gray-50">
      <main className="max-w-4xl mx-auto px-6">
        <div className="flex justify-between mb-4 text-sm text-gray-500">
          {step > 0 ? <button onClick={prev}>← Previous</button> : <span />}
          <Link href="/">Exit</Link>
        </div>

        <ProgressTabs step={step} />

        <div className="bg-white mt-6 rounded-2xl shadow p-6">
          {/* STEP 0 — ABOUT YOU */}
          {step === 0 && (
            <>
              <h2 className="text-xl font-semibold mb-4">Tell us about you</h2>

              {/* Name */}
              <div className="mb-4">
                <label className="font-medium">Name *</label>
                <input
                  className="w-full border px-3 py-2 rounded mt-1"
                  value={form.name}
                  onChange={(e) => updateField("name", e.target.value)}
                />
                {errors.name && (
                  <p className="text-red-600 text-sm mt-1">{errors.name}</p>
                )}
              </div>

              {/* Phone */}
              <div className="mb-4">
                <label className="font-medium">Phone *</label>
                <input
                  className="w-full border px-3 py-2 rounded mt-1"
                  value={form.phone}
                  onChange={(e) => updateField("phone", e.target.value)}
                />
                {errors.phone && (
                  <p className="text-red-600 text-sm mt-1">{errors.phone}</p>
                )}
              </div>

              {/* Age */}
              <div className="mb-4">
                <label className="font-medium">Age *</label>
                <input
                  type="number"
                  className="w-full border px-3 py-2 rounded mt-1"
                  value={form.age}
                  onChange={(e) => updateField("age", e.target.value)}
                />
                {errors.age && (
                  <p className="text-red-600 text-sm mt-1">{errors.age}</p>
                )}
              </div>

              {/* Gender */}
              <div>
                <label className="font-medium">Gender *</label>
                <div className="flex gap-3 mt-2">
                  {["male", "female", "other"].map((g) => (
                    <button
                      key={g}
                      onClick={() => updateField("gender", g)}
                      className={`px-5 py-2 rounded ${
                        form.gender === g
                          ? "bg-emerald-300"
                          : "bg-gray-100"
                      }`}
                    >
                      {g.toUpperCase()}
                    </button>
                  ))}
                </div>
                {errors.gender && (
                  <p className="text-red-600 text-sm mt-1">{errors.gender}</p>
                )}
              </div>
            </>
          )}

          {/* STEP 1 — HAIR HEALTH */}
          {step === 1 && (
            <>
              <h2 className="text-xl font-semibold mb-4">Hair Health</h2>

              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                {stageImages.map((s) => (
                  <div
                    key={s.id}
                    onClick={() => updateField("stage", s.id)}
                    className={`rounded-xl border p-4 cursor-pointer ${
                      form.stage === s.id
                        ? "ring-2 ring-emerald-400"
                        : "border-gray-200"
                    }`}
                  >
                    <img src={s.img} className="w-full mb-3" />
                    <p className="font-medium">{s.label}</p>
                  </div>
                ))}
              </div>

              {errors.stage && (
                <p className="text-red-600 text-sm mt-2">{errors.stage}</p>
              )}

              <div className="mt-6 space-y-6">
                {hairQuestions.map((q) => (
                  <QuestionCard
                    key={q.id}
                    question={q.q}
                    options={q.options}
                    value={form.hair[q.id]}
                    onChange={(v) => updateNested("hair", q.id, v)}
                  />
                ))}
              </div>
            </>
          )}

          {/* STEP 2 — INTERNAL HEALTH */}
          {step === 2 && (
            <>
              <h2 className="text-xl font-semibold mb-4">Internal Health</h2>

              <div className="space-y-6">
                {internalQuestions.map((q) => (
                  <QuestionCard
                    key={q.id}
                    question={q.q}
                    options={q.options}
                    value={form.internal[q.id]}
                    onChange={(v) => updateNested("internal", q.id, v)}
                  />
                ))}
              </div>
            </>
          )}

          {/* STEP 3 — SCALP ASSESSMENT */}
          {step === 3 && (
            <>
              <h2 className="text-xl font-semibold mb-4">Scalp Assessment</h2>

              <PhotoUploader
                file={form.scalpPhoto}
                onChange={(file) => updateField("scalpPhoto", file)}
              />

              {errors.scalpPhoto && (
                <p className="text-red-600 text-sm mt-2">{errors.scalpPhoto}</p>
              )}
            </>
          )}

          {/* BUTTONS */}
          <div className="flex justify-end mt-6">
            {step < 3 ? (
              <button
                onClick={next}
                className="px-6 py-2 bg-emerald-600 text-white rounded"
              >
                Next →
              </button>
            ) : (
              <button
                onClick={submit}
                className="px-6 py-2 bg-indigo-600 text-white rounded"
              >
                Submit Assessment
              </button>
            )}
          </div>
        </div>
      </main>
    </div>
  );
}

